.navbar {
    /* height: 5rem; */
    background: rgb(30,30,30);
    width: 100vw;
    max-width: 75rem;
    margin: 0 auto; 
    padding: 1rem 2rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: sticky;
    top: 0;
    z-index: 3;
}

.logo{
    object-fit: cover;
    height: 5rem;
    width: 5rem;
}
.desktopmenuListItem{
 margin: 2rem;
 cursor: pointer;
}
.desktopmenuListItem:hover{
    color: yellow;
    padding-bottom: 0.5rem;
    border-bottom: 2px solid yellow;
}
.desktopMenuBtn{
    cursor: pointer;
    background:white ;
    color: black;
    border: none;
    align-content: center;
    justify-content: center;
    padding: 0.5rem  2rem;
    border-radius: 2rem;
}

.active{
    color: yellow;
    padding-bottom: 0.5rem;
    border-bottom: 2px solid yellow;
}

.mobMenu{
    display: none;
    object-fit: cover;
    height: 1.8rem;
}

.navMenu{
    position: absolute;
    top: 4rem;
    right: 2rem;
    z-index: 2;
    display: flex;
    flex-direction: column;
    padding: 0.5rem;
    height: fit-content;
    min-height: 15rem;
    background: rgb(40,40,40);
    border-radius: 1rem;
}

.listItem{
   color: white; 
   padding: 0.5rem 2rem;
   margin: 0.25rem;
   background: rgb(30,30,30);
}
 
@media screen and ( max-width:720px){
  .mobMenu{
    display: flex;
  }  
  .desktopmenu{
    display: none;
  }
  .desktopMenuBtn{
    display: none;
  }
}