

#intro {
    height: calc(100vh - 4rem);
    max-width: 75rem;
    margin: 0 auto;
    overflow: hidden;
    position: relative;
}

.bg {
    position: absolute;
    top: -4rem;
    right: 0;
    z-index: -1;
    object-fit: cover;
    height: 100vh;
}

.introContent {
    height: 100%;
    padding: 2rem;
    font-size: 3rem;
    font-weight: 700;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.hello {
    font-size: 1.75rem;
    font-weight: 100;
}

.introName {
    color: yellow;
}

.introPara {
    font-size: medium;
    font-weight: 300;
}

.btn {
    cursor: pointer;
    background: white;
    margin: 1rem 0;
    padding: 0.75rem 2rem;
    border-radius: 2rem;
}

@media screen and (max-width: 768px) {
    .bg {
        right: -5vw;
    }

    .introContent {
        font-size: 5vw;
    }

    .hello {
        font-size: 2.5vw;
    }
}
