   #skills {
    overflow: hidden;
    width: 100%;
    max-width: 65rem;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
  }
  
  .skillTitle {
    font-size: 3rem;
    font-weight: 600;
    margin-top: 3rem;
    margin-bottom: 1.5rem;
  }
  
  .skillDesc {
    font-weight: 300;
    font-size: 1rem;
    max-width: 51rem;
    padding: 0 2rem;
  }
  
  .skillBars {
    margin: 1.5rem;
    width: 100%;
    max-width: 80%;
    text-align: left;
  }
  
  .skillBar {
    display: flex;
    margin: 1rem;
    padding: 1.5rem 2rem;
    border-radius: 0.5rem;
    background: rgb(50, 50, 50);
  }
  
  .skillBarImg {
    object-fit: cover;
    height: 4rem;
    width: 4rem;
    margin-right: 1rem;
  }
  
  .skillBarText p {
    font-size: 1rem;
    font-weight: 200;
    margin: 0;
  }
  
  @media screen and (max-width: 1200px) {
    .skillBars {
      max-width: 90%;
    }
  }
  
  @media screen and (max-width: 992px) {
    .skillBars {
      max-width: 95%;
    }
  }
  
  @media screen and (max-width: 768px) {
    .skillBars {
      max-width: 100%;
    }
    .skillBarImg {
      height: 3rem;
      width: 3rem;
    }
  }
  
  @media screen and (max-width: 480px) {
    .skillBarText p {
      font-size: 3vw;
    }
    .skillBarImg {
      height: 2.25rem;
      width: 2.25rem;
    }
    .skillBarText h2 {
      font-size: 5vw;
    }
  } 
  
  