#contactPage{
    min-width: calc(100vh-4rem);
    width: 100vw;
    max-width: 60rem;
    margin: 0 auto;
    text-align: center;

}
.contactPageTitle{
    font-size: 3rem;
    margin-bottom: 1rem; 
}

.projectPageTitle{
  font-size: 3rem;
margin-bottom: 1rem; 
}

.projectDesc{
  font-weight: 300;
  font-size: 1rem;  
}

#project{
  padding: 2rem;
}

.contactDesc{
  padding: 1rem;
  font-size: medium;
  font-weight: 300;

}

.contactForm{
  margin: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 90vw;
  max-width: 60rem;
}

.name,.email,.msg{
  font-size: medium;
  width: 100%;
  margin: 0.5rem;
  padding: 0.5rem 1rem;
  color: white;
  border-radius: 0.5rem;
  background: rgb(40,40,40);
}


.submitBtn{
  cursor: pointer;
    margin: 1rem;
    background: white;
    border: none;
    border-radius: 0.5rem;
    padding: 0.75rem 3.5rem;  
}

.links{
   margin: 1rem 0;
   display: flex;
   flex-wrap: wrap;
}

.link{
    object-fit: cover;
    height: 3rem;
    width: 3rem;
    margin: 0 0.75rem;

}